import React from "react";

import { A11y, Autoplay, EffectFade } from "swiper";

import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import "swiper/css/autoplay";
import "swiper/css/effect-fade";
import "swiper/css/effect-fade";
const HomeCarousel = (props) => {
  const name = props.name;
  const __dirName = `${process.env.PUBLIC_URL}/assets/img/${name}/`;

  return (
    <>
      {/* Home Carousel */}
      {/* <div className="w-auto sm:w-screen xs:w-36 md:w-96 h-auto md:h-75 sm:mt-24"> */}
      <div className="w-auto sm:w-screen xs:w-36 md:w-96 h-fit">
        <Swiper
          modules={[A11y, Autoplay, EffectFade]}
          spaceBetween={50}
          loop={true}
          autoplay={{ delay: 3000 }}
          effect={"fade"}
        >
          <SwiperSlide>
            <img
              src={__dirName + "1.jpg"}
              alt="home1"
              // className="object-cover h-64 w-96 "
              className="lg:object-fill lg:h-80 h-80 object-cover"
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "2.jpg"}
              alt="home2"
              // className="object-cover h-64 w-96"
              className="lg:object-fill lg:h-80 h-80 object-cover"
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "3.jpg"}
              alt="home3"
              // className="object-cover h-64 w-96"
              className="lg:object-fill lg:h-80 h-80 object-cover"
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "4.jpg"}
              alt="home4"
              // className="object-cover h-64 w-96"
              className="lg:object-fill lg:h-80 h-80 object-cover"
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "5.jpg"}
              alt="home5"
              // className="object-cover h-64 w-96"
              className="lg:object-fill lg:h-80 h-80 object-cover"
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "6.jpg"}
              alt="home6"
              // className="object-cover h-64 w-96"
              className="lg:object-fill lg:h-80 h-80 object-cover"
            />
          </SwiperSlide>
        </Swiper>
      </div>
    </>
  );
};

export default HomeCarousel;
