import React from "react";

import { A11y, Autoplay, EffectFade } from "swiper";

import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/scrollbar";
import "swiper/css/autoplay";
import "swiper/css/effect-fade";
import "swiper/css/effect-fade";
const ShowCarousel = (props) => {
  const name = props.name;
  const __dirName = `${process.env.PUBLIC_URL}/assets/img/${name}/`;

  return (
    <>
      {/* Electro Mechanical Carousel */}
      {/* <div className="lg:h-full w-auto sm:w-screen xs:w-36 md:w-96 xs:h-auto md:h-75 sm:mt-24"> */}
      <div className="w-auto sm:w-screen xs:w-36 md:w-96 h-fit">
        <Swiper
          modules={[A11y, Autoplay, EffectFade]}
          spaceBetween={50}
          loop={true}
          autoplay={{ delay: 3000 }}
          effect={"fade"}
        >
          <SwiperSlide>
            <img
              src={__dirName + "1.jpg"}
              alt="em1"
              className="object-fill h-80 "
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "2.webp"}
              alt="em2"
              className="object-fill h-80 "
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "3.JPG"}
              alt="em3"
              className="object-fill h-80 "
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "4.jpg"}
              alt="em4"
              className="object-fill h-80 "
            />
          </SwiperSlide>
          <SwiperSlide>
            <img
              src={__dirName + "5.jpeg"}
              alt="em5"
              className="object-fill h-80 "
            />
          </SwiperSlide>
        </Swiper>
      </div>
    </>
  );
};

export default ShowCarousel;
