import React from "react";

const Services = () => {
  const __dirName = `${process.env.PUBLIC_URL}/assets/img/`;
  return (
    <div className="lg:mt-16 bg-services-bg bg-cover bg-center bg-fixed">
      <section className="text-gray-600 body-font">
        <div className="container px-5 py-24 mx-auto">
          <div className="flex flex-col text-center items-center justify-center w-full mb-32">
            <h1 className="text-white shadow-xl shadow-cyan-200 lg:w-1/2 lg:flex lg:items-center lg:justify-center text-4xl font-medium font-audioWide bg-black bg-opacity-50 rounded-large">
              Some of the services we provide
            </h1>
          </div>

          <div className="flex flex-wrap ">
            {/* Service Supply */}
            <div className="p-4 md:w-1/3">
              <div className="card lg:w-96 opacity-100 text-white">
                <figure>
                  <img src={`${__dirName}/services-img.jpg`} alt="Service" />
                </figure>
                <div className="card-body bg-black bg-opacity-80 text-center">
                  <h2 className="card-title flex justify-center items-center">
                    Service Supply
                  </h2>
                  <p>
                    We provide a multitude of services to our customers in
                    person or online.
                  </p>
                </div>
              </div>
            </div>

            {/* Import Supply */}
            <div className="p-4 md:w-1/3">
              <div className="card opacity-100 lg:w-96 text-white">
                <figure>
                  <img src={`${__dirName}/import1-img.jpg`} alt="Import" />
                </figure>
                <div className="card-body bg-black bg-opacity-80 text-center">
                  <h2 className="card-title flex justify-center items-center">
                    Import Supply
                  </h2>
                  <p>
                    We deliver cost-effective items from overseas on customers'
                    requests providing highest quality products.
                  </p>
                </div>
              </div>
            </div>

            {/* Installation */}
            <div className="p-4 md:w-1/3">
              <div className="card lg:w-96 opacity-100 text-white">
                <figure>
                  <img src={`${__dirName}/install-img.jpg`} alt="install" />
                </figure>
                <div className="card-body bg-black bg-opacity-80 text-center">
                  <h2 className="card-title flex justify-center items-center ">
                    Installation
                  </h2>
                  <p>
                    We will install any and all products to your business or
                    home on delivery.
                  </p>
                </div>
              </div>
            </div>
          </div>

          <div className="flex flex-wrap -m-4 lg:mt-36">
            {/* Maintainance */}
            <div className="p-4 md:w-1/3">
              <div className="card lg:w-96 opacity-100 text-white">
                <figure>
                  <img
                    src={`${__dirName}/maintainance-img.jpg`}
                    alt="install"
                  />
                </figure>
                <div className="card-body bg-black bg-opacity-80 text-center">
                  <h2 className="card-title flex justify-center items-centertext-center">
                    Maintainance
                  </h2>
                  <p>
                    We provide on-site Maintainance incase of malfunctions of
                    our products.
                  </p>
                </div>
              </div>
            </div>

            {/* Comission */}
            <div className="p-4 md:w-1/3">
              <div className="card lg:w-96 opacity-100 text-white">
                <figure>
                  <img src={`${__dirName}/Comission2.jpg`} alt="Commission" />
                </figure>
                <div className="card-body bg-black bg-opacity-80 text-center">
                  <h2 className="card-title flex justify-center items-centertext-center">
                    Comissioning
                  </h2>
                  <p>We offer advice and counceling for those that seek it.</p>
                </div>
              </div>
            </div>

            {/* Distribution */}
            <div className="p-4 md:w-1/3">
              <div className="card lg:w-96 opacity-100 text-white">
                <figure>
                  <img src={`${__dirName}/distribution.jpg`} alt="install" />
                </figure>
                <div className="card-body bg-black bg-opacity-80 text-center">
                  <h2 className="card-title flex justify-center items-centertext-center">
                    Distribution
                  </h2>
                  <p>
                    We provide transport and delivery of large amounts of
                    equipment on a moment's notice.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default Services;
